import './../style/register.css'
import { EmailAlert, PwAlert, PwCheck } from './RegisterAlert';
import { useState } from 'react';
import { useSelector } from 'react-redux';

function RegisterUserInfo({ onChangeIndex, onBackIndex }) {
  const id = useSelector((state) => state.user.length)-1
  console.log(id)
  const email = useSelector((state) => state.user[id].email)
  console.log(email)
  return (
    <section>
      <div className='register-form'>
        <h2></h2>
        <div className='form user-form'>
          <article>
            <div>
              <h3> </h3>
              <input placeholder='  .'/>
            </div>
            <div>
              <h3></h3>
              <input placeholder='10 '/>
              {/* <input placeholder=' .' name='pw' onChange={onChangeAccount}/> */}
            </div>
            <div>
              <h3></h3>
              <input value={email} readOnly/>
            </div>
            <div className='user-phone'>
              <h3> </h3>
              <div className='phone-numb'>
                <input placeholder=' '/>
                <button> </button>
                <input placeholder=' '/>
                <button></button>
              </div>
            </div>
          </article>
          <footer>
            <button className='prev-btn' onClick={() => onBackIndex()}></button>
            <button className='next-btn' onClick={() => onChangeIndex()}></button>
          </footer>
        </div>
      </div>
    </section>
  )
}
export default RegisterUserInfo;