import './../style/register.css'
import { useState } from 'react';
import AddressAPI from './AddressAPI';
import styled from 'styled-components';
import { Link } from 'react-router-dom';

const Select = styled.select`
  color: ${props => props.color};
`
const Option = styled.option`
  color: 'black';
`
function FacilityInfo() {
  const [addressData, setAddressData] = useState(0)
  const [color, setColor] = useState('#A8A8A8')
  const getAddressData = (data) => {
    setAddressData(data)
    console.log(addressData)
    console.log('')
  };

  const changeOption = (e) => {
    const value = e.target.value;
    value === '' ? setColor('#A8A8A8') : setColor('black')
  }

  return (
    <section>
      <div className='register-form'>
        <h2></h2>
        <div className='form facility-form'>
          <article className='facility-type'>
            <div>
              <h3> </h3>
              <Select onChange={changeOption} color={color}>
                <Option label=''></Option>
                <Option id='1'>option1</Option>
                <Option id='2'>option2</Option>
              </Select>
            </div>
            <div className='name-wrap'>
              <h3> </h3>
              <input className='name-input' placeholder='  .'/>
            </div>
          </article>
          <article className='facility-add'>
              <h3> </h3>
              <div className='code-wrap'>
                <input className='code-input' placeholder=' ' value={addressData.code} readOnly/>
                <AddressAPI getAddressData={getAddressData}/>
              </div>
              <input placeholder=' ' value={addressData.add} readOnly/>
              <input placeholder=' '/>
            <div>
              <h3> </h3>
              <input placeholder=' .'/>
            </div>
          </article>
          <Link to='/welcome'><button>  </button></Link>
        </div>
      </div>
    </section>
  )
}
export default FacilityInfo;