import { useState } from 'react';
import { useSelector } from 'react-redux';
import { Link, useNavigate } from 'react-router-dom';
import './../style/login.css'

function Login() {
  const userData = useSelector((state) => state.user)
  const [account, setAccount] = useState([])
  const [id, setId] = useState(0)
  const navigate = useNavigate()

  const onLoginAccount = (e) => {
    setAccount({
      ...account,
      [e.target.name]: e.target.value
    })
  }

  const onCheckLogin = () => {
    userData.map((data, i) => {
      console.log(userData[i])
      if (data.email === account.email) {
        console.log(' ')
        setId(i)
        if (userData[`${id}`].pw === account.pw) {
          console.log(' ')
          navigate('/')
          alert(' ')
        }
      }
      console.log('')
    })
  }
console.log(account)
console.log(account.email)

  return (
    <section>
      <div className='login-form'>
        <p className='login-img'></p>
        <div className='input-form'>
          <div className='check-input'>
            <input placeholder='  .' name='email' onChange={onLoginAccount}/>
            <input type='password' placeholder='  .' name='pw' onChange={onLoginAccount}/>
            <button onClick={onCheckLogin}></button>
          </div>
          <div className='check-pw'>
            <ul>
              <li>
                <input type='checkbox' id='autoLogin'/>
                <label for='autoLogin'/>
              </li>
              <li>
                <Link to='/findpw'>  ?</Link>
              </li>
            </ul>
          </div>
        </div>
        <p className='check-register'>VICTOK  ? <Link to='/register'></Link></p>
      </div>
    </section>
  )
}
export default Login;