import { useState } from 'react';
import './../style/locker.css'
import markIcon from './../assets/icons/exclamation-mark.png';

const lockerStatus = [
  {id: 0, name: '', phone: '010-1234-1234', type: '', numb: 123, startDate: '21.12.23', endDate: '21.12.24', remainingDate: 1},
  {id: 1, name: '', phone: '010-1234-1234', type: '', numb: 123, startDate: '21.09.23', endDate: '21.12.24', remainingDate: 93},
]
function Locker() {
  const [checkedArr, setCheckedArr] = useState([])
  let [count, setCount] = useState(0)
  // const [display, setDisplay] = useState('none')

  const singleCheck = (checked, id) => {
    console.log(id)
    if (checked) {
      setCheckedArr(prev => [...prev, id])
    } else {
      setCheckedArr(checkedArr.filter((e) => e !== id))
    }
  };

  const allCheck = (checked) => {
    if (checked) {
      const idArr = [];
      lockerStatus.forEach((e) => idArr.push(e.id))
      setCheckedArr(idArr)
    } else {
      setCheckedArr([])
    }
  }

  const onCount = () => {
    setCount++
  }

  return (
    <>
      <section>
        <div className='locker-form'>
          <article className='locker-30days-form'>
            <h2>  30 </h2>
            <table>
              {lockerStatus.map((data, i) => (
                <>
                  {(data.remainingDate < 31) 
                    ? <thead>
                        <tr>
                          <th></th>
                          <th></th>
                          <th></th>
                          <th></th>
                          <th></th>
                          <th></th>
                          <th></th>
                      </tr>
                    </thead>
                    : null
                  }
                </>
              ))}
              {lockerStatus.map((data, i) => (console.log(data.remainingDate < 31)&&data.remainingDate < 31 === null 
              ?
                <div className='locker-30days-null'>
                  <p className='mark-icon'><img src={markIcon}/></p>
                  <p>30    .</p>
                </div>
              : null ) )}
              <tbody>
                {lockerStatus.map((data, i) => (
                  <>
                    {(data.remainingDate < 31) 
                      ? <tr>
                        <td>{data.name}</td>
                        <td>{data.phone}</td>
                        <td>{data.type}</td>
                        <td>{data.numb}</td>
                        <td>{data.startDate}</td>
                        <td>{data.endDate}</td>
                        <td>{data.remainingDate}</td>
                        </tr>
                      : null
                    }
                  </>
                ))}
              </tbody>
            </table>
          </article>
          <article className='locker-status-form'>
            <div className='locker-status-header'>
              <div className='left-btn'>
                <h2> </h2>
                <button></button>
                <button className='array-btn'></button>
              </div>
              <div className='right-btn'>
                <input placeholder=' /   '/>
                <button className='add-btn'></button>
                <button className='select-del-btn'> </button>
              </div>
            </div>
            <table>
              <thead>
                <tr>
                  <th>
                  <div className='check-list-input'>
                    <input type='checkbox' id='checkAll' 
                      onChange={(e) => allCheck(e.target.checked)} 
                      checked={checkedArr.length === lockerStatus.length ? true : false}
                    />
                    <label for='checkAll'/>
                    </div>
                    </th>
                  <th></th>
                  <th></th>
                  <th></th>
                  <th></th>
                  <th></th>
                  <th></th>
                  <th></th>
                </tr>
              </thead>
              <tbody>
                {lockerStatus.map((data, i) => (
                  <tr>
                    <td className='check-wrap'>
                      <div className='check-list' key={i}>
                        <div className='check-list-input'>
                          <input type='checkbox' id={`check${data.id}`} 
                            onChange={(e) => singleCheck(e.target.checked, data.id)} 
                            checked={checkedArr.includes(data.id) ? true : false}
                          />
                          <label for={`check${data.id}`}/>
                        </div>
                      </div>
                    </td>
                    <td>{data.name}</td>
                    <td>{data.phone}</td>
                    <td>{data.type}</td>
                    <td>{data.numb}</td>
                    <td>{data.startDate}</td>
                    <td>{data.endDate}</td>
                    <td>{data.remainingDate}</td>
                  </tr>
                ))}
              </tbody>
            </table>
          </article>
        </div>
      </section>
    </>
  )
}
export default Locker;