import { useState } from 'react';
import { Link } from 'react-router-dom';
import dia from './../assets/img/ 6961.svg';

function Header() {
  const [tab, setTab] = useState();
  const tabData = [
    // {id: 0, title: '', link: '/'}, 
    {id: 0, title: '', link: '/'}, 
    {id: 1, title: '', link: '/locker/status'},
    {id: 2, title: '', link: '/'},
  ];
  const[active, setActive] = useState();

  const onActive = (e) => {
    const tabId = e.target.getAttribute('value')
    console.log(tabId)
    console.log(active)
    setTab(tabId);
    setActive(tabId);
    if (!tabId) {
      console.log('')
    }
  }

  const offActive = () => {
    setTab()
    setActive()
  }

  return (
    <header className='header'>
      <div className='header-nav'>
        <ul className='main-nav'>
          <Link to='/'>
            <li className='header-logo' onClick={offActive}></li>
          </Link>
          {
            tabData.map((data, i) => {
              return (
                <Link to={data.link}>
                  <li key={i}>
                    <a className={i == active ? 'active' : ''} value={i} 
                    onClick={onActive}>{data.title}</a>
                  </li>
                </Link>
              )
            })
          }
          {/* <li></li>
          <li></li>
          <li></li> */}
        </ul>
        <ul className='sub-nav'>
          <Link to='/'>
            <li></li>
          </Link>
          <img src={dia} className='sub-dia' alt='dia'/>
          <Link to='/login'>
            <li></li>
          </Link>
          <img src={dia} className='sub-dia' alt='dia'/>
          <Link to='/register'>
            <li></li>
          </Link>
        </ul>
      </div>  
    </header>
  )
}

export default Header;